.control{
  width:100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
}
.control label{
  font-weight: bold;
  color: #212529;
  margin-left: 10px;
}
.control input{
  width: 70%;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid gray;
  color: #495057;
  font-size: 15px;
}
.button{
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
}